export function basename(filename) {
    const end = filename.lastIndexOf("/");
    const basename = filename.substring(end === -1 ? 0 : end);
    return basename;
}
function join(...paths) {
    const pathParts = [];
    for (const path of paths) {
        for (const part of path.split("/")) {
            switch (part) {
                case "":
                case ".":
                    break;
                case "..":
                    pathParts.pop();
                    break;
                default:
                    pathParts.push(part);
            }
        }
    }
    return "/" + pathParts.join("/");
}
export class FileSystem {
    adapter;
    wd = "/";
    stack = [];
    constructor(adapter = new RecordFileSystemAdapter()) {
        this.adapter = adapter;
    }
    cwd() {
        return this.wd;
    }
    cd(dir) {
        this.wd = this.p(dir);
    }
    pushd(dir) {
        this.stack.push(this.wd);
        this.cd(dir);
    }
    popd() {
        if (this.stack.length > 0) {
            this.wd = this.stack.pop();
        }
    }
    stat(path) {
        return this.adapter.stat(this.p(path));
    }
    scandir(path) {
        return this.adapter.scandir(this.p(path));
    }
    readdir(path) {
        return this.adapter.readdir(this.p(path));
    }
    copyFile(from, to) {
        return this.adapter.copyFile(this.p(from), this.p(to));
    }
    readFile(path) {
        return this.adapter.readFile(this.p(path));
    }
    writeFile(path, contents) {
        return this.adapter.writeFile(this.p(path), contents);
    }
    rm(path) {
        return this.adapter.rm(this.p(path));
    }
    p(path) {
        return path[0] === "/" ? path : join(this.cwd(), path);
    }
}
export class RecordFileSystemAdapter {
    fs;
    constructor(fs = {}) {
        this.fs = fs;
    }
    stat(path) {
        return new Promise((resolve, reject) => {
            if (this.fs[path] != null) {
                resolve({
                    name: basename(path),
                    isDirectory() {
                        return false;
                    },
                    isFile() {
                        return true;
                    },
                });
            }
            else {
                reject();
            }
        });
    }
    async scandir(path) {
        return (await this.readdir(path)).map((name) => {
            let isFile = this.fs[join(path, name)] !== undefined;
            return {
                name,
                isDirectory() {
                    return !isFile;
                },
                isFile() {
                    return isFile;
                },
            };
        });
    }
    readdir(path) {
        if (!path.endsWith("/"))
            path += "/";
        return new Promise((resolve) => {
            let dir = new Set();
            for (const filename of Object.keys(this.fs)) {
                if (filename.startsWith(path)) {
                    const end = filename.indexOf("/", path.length + 1);
                    const basename = filename.substring(path.length, end === -1 ? undefined : end);
                    dir.add(basename);
                }
            }
            return resolve([...dir].sort());
        });
    }
    copyFile(from, to) {
        return new Promise((resolve) => {
            this.fs[to] = this.fs[from];
            resolve();
        });
    }
    readFile(path) {
        return new Promise((resolve, reject) => {
            let file = this.fs[path];
            if (file === undefined) {
                const error = new Error(`File Not Found ${path}`);
                reject(error);
            }
            else {
                resolve(file);
            }
        });
    }
    writeFile(path, contents) {
        return new Promise((resolve) => {
            this.fs[path] = contents;
            resolve();
        });
    }
    rm(path) {
        return new Promise((resolve) => {
            delete this.fs[path];
            resolve();
        });
    }
}
export class LocalStorageFileSystemAdapter extends RecordFileSystemAdapter {
    constructor() {
        super(window.localStorage);
    }
}
export class ObjectFileSystemAdapter extends RecordFileSystemAdapter {
}
export async function reset(fs, tree) {
    for (const [path, file] of Object.entries(tree)) {
        if (typeof file === "string") {
            await fs.writeFile(path, file);
        }
        else {
            fs.cd(path);
            await reset(fs, file);
            fs.cd("..");
        }
    }
}
